import React from "react";
import { LuAlertTriangle } from "react-icons/lu";
import { AiOutlineAlert } from "react-icons/ai";
import { AiOutlineBug } from "react-icons/ai";
import { getAlertType, getAlertInfoViaEvent } from "@/utils/helper";

export default function AlertName({ data = [] }) {
  const alertInfo = getAlertInfoViaEvent(data);
  return (
    <div className="flex items-center gap-2 w-[350px]">
      <div id="Icon">
        {alertInfo?.icon ? (
          <img
            src={alertInfo?.icon}
            alt="Alert Icon"
            width={38}
            height={38}
            className="min-w-[38px]"
          />
        ) : null}
      </div>
      <div className="w-full">
        <h4 className="text-sm font-medium text-text-primary">
          {alertInfo?.title}
        </h4>
        <p className="text-xs text-text-secondary">{alertInfo?.message}</p>
      </div>
    </div>
  );
}
